import React, {useEffect, useState} from 'react';

import useStyles from "./FormStyles"
import {Button, Paper, TextField, Typography} from "@material-ui/core";
import FileBase from "react-file-base64";
import {useDispatch, useSelector} from "react-redux";
import {createPost, updatePost} from "../../redux/features/post/thunks/postThunk";

function Form({currentId, setCurrentId}) {
   const post = useSelector(state => currentId ? state.post.posts.find((p) => p._id === currentId) : null)
   const [postData, setPostData] = useState({
      title: "",
      message: '',
      tags: '',
      selectedFile: ''
   });
   const classes = useStyles()
   const dispatch = useDispatch()

   const user = JSON.parse(localStorage.getItem('profile'))


   useEffect(() => {
      if (post) setPostData(post)
   }, [post]);


   function handleSubmit(event) {
      event.preventDefault()

      if (currentId === 0) {
         // send post to api
         dispatch(createPost({...postData, name: user?.result?.name}))
      } else {
         // Update post
         dispatch(updatePost({id: currentId, post: {...postData, name: user?.result?.name}}))
      }

      clear()
   }

   if (!user?.result?.name) {
      return (
          <Paper className={classes.paper}>
             <Typography variant="h6" align="center">
                Please Sign In to create your own memories and like other's memories.
             </Typography>
          </Paper>
      )
   }

   function clear() {
      setCurrentId(null)
      setPostData({
         title: "",
         message: '',
         tags: '',
         selectedFile: ''
      })
   }

   //  *******************  JSX Code ********************
   return (
       <Paper className={classes.paper} elevatione={6}>
          <form autoComplete="off" noValidate className={`${classes.root} ${classes.form}`} onSubmit={handleSubmit}>
             <Typography variant="h6">{currentId ? 'Update' : 'Create'} a Memory</Typography>
             <TextField name="title" variant="outlined" label="Title" fullWidth value={postData.title} onChange={event => setPostData({
                ...postData,
                title: event.target.value
             })}/>
             <TextField name="message" variant="outlined" label="Message" fullWidth value={postData.message} onChange={event => setPostData({
                ...postData,
                message: event.target.value
             })}/>
             <TextField name="tags" variant="outlined" label="Tags" fullWidth value={postData.tags} onChange={event => setPostData({
                ...postData,
                tags: event.target.value.split(',')
             })}/>
             <div className={classes.fileInput}>
                <FileBase
                    type="file"
                    multible={false}
                    onDone={({base64}) => setPostData({...postData, selectedFile: base64})}
                />
             </div>
             <Button className={classes.buttonSubmit} variant="contained" color="primary" size="large" type="submit" fullWidth>{currentId ? 'Update' : 'Submit'}</Button>
             <Button className={classes.buttonClear} variant="contained" color="secondary" size="small" onClick={clear} fullWidth>Clear</Button>
          </form>
       </Paper>
   );
}

export default Form;